<template>
  <div class="header-wrap">
    <div class="container header">
      <div class="header-left">
        <router-link to="/">
          <img class="logo"  src="../../images/home/logo2.png" alt="">
        </router-link>  
        <div>API开放平台</div>
      </div>
      <div class="header-right">
        <div class="search-wrap" @mouseleave="isHide" :style="`background:${opacity ? '#f6f6f6' : ''}`">
          <div class="search-left" :style="`opacity:${opacity};pointer-events: ${opacity ? '' : 'none'};`">
            <el-select :style="`display:  ${opacity ? '' : 'none'}`" class="select-input" size="mini" v-model="value" placeholder="请选择" @visible-change="selectchange">
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-select>
            <el-input class="serach-inp" clearable type="text" v-model="aa" placeholder="输入关键字搜索"></el-input>
          </div>
          <i class="el-icon-search icon-search" @mouseover="isblock"></i>
        </div>
        <div class="menu-wrap">
         <el-menu  mode="horizontal" class="nav_container" :collapse-transition="false" text-color="#030303" active-text-color="#CB1818" :default-active="menucheck">
            <el-menu-item index="platformHome" @click="toPath('platformHome')">
              首页
            </el-menu-item>
            <el-menu-item index="apiList"  @click="toPath('apiList')">开放API</el-menu-item>
            <el-menu-item index="documents" @click="toPath('documents')">文档中心</el-menu-item>
          </el-menu>
        </div>
        <div class="login-or-register">
          <router-link to="/my">登录</router-link>
          ｜
          <router-link to="/register">注册</router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      opacity: 0,  // 控制搜索框显示还是隐藏
      selectStatus: false, // 下拉菜单是否展开
      aa: '',
      options: [{
          value: '选项1',
          label: 'api'
        }, {
          value: '选项2',
          label: '场景'
        }]
    }
  },
  computed:{
    menucheck(){
      if(this.$route.fullPath.indexOf('platformHome') !== -1) {
        return 'platformHome'
      }
      if(this.$route.fullPath.indexOf('apiList') !== -1) {
        return 'apiList'
      }
      if(this.$route.fullPath.indexOf('documents') !== -1) {
        return 'documents'
      }
      
    }
  },
  methods:{
    isblock() {
      this.opacity = 1
    },
    isHide(e) {
      if(!this.selectStatus){
        this.opacity = 0
      }
    },
    selectchange(e){
      this.selectStatus = e
    },
    toPath(pathName) {
      this.$router.push(`/${pathName}`)
    }
  }
}
</script>

<style type="text/css" scoped>
.header-wrap {
  width: 100%;
  background: #fff;
  border-bottom: 4px solid #CC0C0C;
}
.header {
  display: flex;
  justify-content: space-between;
  height: 62px;
}
.header-left {
   display: flex;
   align-items: center;
}
.header-left > div {
  color: #1B3148;
  font-size: 17.06px;
  padding-left: 17px;
  position: relative;
}
.header-left > div:after {
   position: absolute;
   width: 1px;
   height: 26px;
   background: #EEEEEE;
   content: '';
   display: block;
   left: 0;
   top: 50%;
   transform: translate(0,-50%);
}
.logo {
  width: 147px;
  height: 42px;
  margin-right: 17px;
}
.header-right {
  display: flex;
  align-items: center;
}
.search-wrap {
  width: 296px;
  height: 30px;
  border-radius: 30px;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.search-wrap >>> .select-input .el-input  input {
  background: #e0e0e0;
  border: 0;
  color: #333;
  width: 140px;
  height: 30px;
  border-radius: 0;
}
.search-wrap >>> input::-webkit-input-placeholder {
   color: #333;
}
.serach-inp {
  width: 122px;
  font-size: 12px;
  background: none;
  border: 0;
  outline: none;
  
}
.serach-inp >>> input {
  background: #f6f6f6!important;
  width: 122px;
  font-size: 12px;
  background: none;
  border: 0;
  outline: none;
  box-sizing: border-box;
  padding-right: 24px;
  
}
.serach-inp >>>  input::-webkit-input-placeholder {
    font-size: 12px;
}
.icon-search {
  cursor: pointer;
  color: #999;
  font-weight: 500;
  font-weight: 700;
  margin-right: 10px;
  font-size: 18px;
}
.menu-wrap {
  margin-left: 30px;

}
.nav_container {
  border: 0;
  display: flex;
  justify-content: center;
}
.nav_container >>> .el-menu-item{
    border-bottom: 0;   
    font-size: 16px;
    color: #030303;
}
.nav_container >>> .is-active  {
  border-top: 2px solid #CC0C0C;
  background: #eeeeee!important;
  border-bottom: 0!important;;
}
.login-or-register {
  font-size: 16px;
  color: #030303;
  margin-left: 35px;
}
.login-or-register  a {
  text-decoration: none;
  color: #030303;
}
</style>


