<template>
  <div class="wrap-layout">
    <Header/>
    <router-view></router-view>
    <Footer/>
  </div>
</template>

<script>
import Header from '../../components/common/Header'
import Footer from '../../components/common/Footer'
export default {
  components:{
    Header,
    Footer
  }
}
</script>

<style>
.wrap-layout {
    background-color: #f2f2f2;
}
.container {
  width: 1200px;
  min-width: 1200px;
  margin: 0 auto;
}
.common-btn {
  text-align: center;
  background-color: #B90909;
  color: #fff;
  border: 0;
  border-radius: 4px;
  display: block;
}
button{
  cursor: pointer;
}
button:active {
  opacity: .8;
}
</style>


